.Layout-HeaderBar {
  display: -ms-flexbox;
  display: flex;
  font-size: 1rem;
  -ms-flex-align: baseline;
      align-items: baseline;
  padding: 1em 0;
}

.Layout-HeaderBar a,
.Layout-HeaderBar a:visited {
  color: var(--heading-color);
}

.Layout-HeaderBar__title {
  -ms-flex: 1 1;
      flex: 1 1;
  color: var(--heading-color);
  font-size: var(--heading-size);
}

.Layout-HeaderBar__social {
  font-size: 0.7em;
  padding: 0 0 0 1.5em;
}

.Layout-HeaderBar__social:hover::after {
  content: '⤴';
  position: absolute;
}



